import React, { Component } from 'react';
import { Row, Col } from 'antd';
import './Login.css'
class Login extends Component {
    constructor(props) {
        super(props)
        this.state = {
        }
    }
    componentDidMount() {
    }
    switchCheck = () => {
        this.setState(pre => ({
            checkbox:!pre.checkbox
        }))
    }
    inputValue=(p,e)=>{
        if (p === 'account') {
            this.setState({
                account: e.currentTarget.value
            })
        }
        if (p === 'password') {
            this.setState({
                password: e.currentTarget.value
            })
        }
    }
    render() {
        // 在父 route 中，被匹配的子 route 变成 props
        return (
            <Row className='login'>
                <Col span={10} offset={14}>
                    <Col span={24} className='login-itm'>
                        <Col span={12} className='login-inp'>
                            <Col span={2} className='login-ico'>
                                <img src={require('../../images/login_ico0.png')} alt="" />
                            </Col>
                            <Col offset={1} span={21}><input type="text" value={this.state.account} onChange={(e) => this.inputValue('account', e)} placeholder='请输入账号' /></Col>
                        </Col>
                    </Col>
                    <Col span={24} className='login-itm'>
                        <Col span={12} className='login-inp'>
                            <Col span={2} className='login-ico'>
                                <img src={require('../../images/login_ico1.png')} alt="" />
                            </Col>
                            <Col offset={1} span={21}><input type="password"value={this.state.password} onChange={(e) => this.inputValue('password', e)} placeholder='请输入账号' /></Col>
                        </Col>
                    </Col>
                    <Col span={24} className='login-itm-t0'>
                        <Col span={12} className='login-inp'>
                            <Col span={2} className='login-ico'>
                                {
                                    this.state.checkbox === true ? (
                                        <img className='login-chek' onClick={this.switchCheck} src={require('../../images/checkbox_sle.png')} alt="" />
                                    ) : (
                                            <img className='login-chek' onClick={this.switchCheck} src={require('../../images/checkbox_no.png')} alt="" />
                                        )
                                }
                            </Col>
                            <Col offset={1} span={21} style={{ color: '#b7b8be' }}>记住密码</Col>
                        </Col>
                    </Col>
                    <Col span={24} className='login-itm'>
                        <Col span={12} className='login-btn'>
                            登录
                        </Col>
                    </Col>
                </Col>
            </Row>
        )
    }
}

export default Login;
